    return{
      LOAD: true,
      currentRoomId : this.$route.query.roomId,
      bookingup : false,
      amenities: [
        {title:"Wi-Fi",ok:true},
        {title:"Breakfast",ok:false},
        {title:"Mini-Bar",ok:false},
        {title:"Room-Service",ok:true},
        {title:"Television",ok:true},
        {title:"Air-Conditioner",ok:false},
        {title:"Refrigerator",ok:true},
        {title:"Sofa",ok:true},
        {title:"Great-View",ok:false},
        {title:"Smoke-Free",ok:false},
        {title:"Child-Friendly",ok:true},
        {title:"Pet-Friendly",ok:false}
