      icons: [
        img0,img1,img2,img3,img4,img5,img6,img7,img8,img9,img10,img11
      ]
    }
  },
  created(){
    this.LOAD = true
    this.$store.dispatch("getRoomInfo",{ roomId:this.currentRoomId })
  },
  computed:{
    roomInfo() {
      return this.$store.state.room
    },
    imgwall() {
      return this.$imgUrl.currentRoomImg(this.currentRoomId) //
    },
    disabledDates() {
      return this.$store.state.disabledDates
    }
  },
  methods: {
    openGallery(index) {
      this.$refs.lightbox.showImage(index)
    },
    childByValue(childValue){
      this.bookingup = childValue
    }
  },
  watch: {
    ['$store.state.room'](v,oldv){
        this.LOAD = false
    },
    deep:true  // deep 
  }
